import InputBox from "../components/input";
import { useRef, useContext } from "react";
import { Link } from "react-router-dom";
import { UserContext } from "../App";
import { storeInSession } from "../common/session";
import { Toaster, toast } from "react-hot-toast";

import "../assets/input.css";


const UserAuthForm = ({ type }) => {

    const authForm = useRef();

    let { userAuth: { access_token }, setUserAuth } = useContext(UserContext)

    const handleSubmit = (e) => {

        e.preventDefault();

        let emailRegex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        let passwordRegex = /^(?=.*[0-9])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{6,16}$/;

        let form = new FormData(authForm.current);
        let formData = {};

        for(let [key, value] of form.entries()){
            formData[key] = value;
        }

        let { fullname, email, password } = formData;
       
        if(fullname.length < 2){
            return toast.error({ "Error" : "Full name must be at least 2 letters long"})
        }
        if(!email.length){
            return toast.error({ "Error" : "Email is invalid"})
        }
        if(!emailRegex.test(email)){
            return toast.error({ "Error" : "Full name must be at least 2 letters long"})
        }
        if(!passwordRegex.test(password)){
            return toast.error({ "Error" : "Password should be 6 to 20 characters long, with at least 1 numeric, 1 uppercase letter and 1 special character."})
        }

    }


    return (
        <section className="user-auth">
            <form>
                <h1>
                    {type === "Log In" ? "Welcome Back" : "Join Coffee Shop"}
                </h1>

                {
                    type !== "Log In" ?
                    
                    <InputBox
                    name="fullname"
                    type="text"
                    placeholder="Full Name"
                    icon="fa-regular fa-user"
                    />
                    :""
                }
                    <InputBox
                    name="email"
                    type="email"
                    placeholder="Email"
                    onkeyup="showHint(this.value)"
                    icon="fa-regular fa-envelope"
                    />

                    <InputBox
                    name="password"
                    type="password"
                    placeholder="Password"
                    icon="fa-solid fa-lock"
                    />

                <button 
                    className="submitBtn"
                    type="submit"
                    onClick={handleSubmit}
                    >
                    { type }
                </button>
                <div className="">
                    <hr />
                    <p>or</p>
                    <hr />
                </div>
                <button>
                    Login with Google
                </button>

                {
                    type === "Log In" ?
                    <p>
                        Don't have an account ?
                        <Link to="/signup" className="link">
                            Sign up here
                        </Link>
                    </p>
                    :
                    <p>
                        Already a member ?
                        <Link to="/login" className="link">
                            Log in here
                        </Link>
                    </p>
                }

            </form>
        </section>
    )
}

export default UserAuthForm;